.home {
  flex-direction: column;
  margin-top: 3em;
}

.home__name {
  color: var(--clr-primary);
}

.home> :nth-child(2) {
  margin-top: 0.5em;
  font-size: 3rem;
  line-height: 1.2;
  color: var(--clr-fg-alt);
}


.home> :nth-child(3) {
  margin-top: 0.5em;
  font-size: 3.5rem;
  line-height: 1.2;
  color: var(--clr-fg-alt);
}

.home__desc {
  font-size: 1rem;
  max-width: 70%;
  text-align: center;
}

.home__desc,
.home__contact {
  margin-top: 2.4em;
}

.home .link--icon {
  margin-right: 0.8em;
}

.home .btn--outline {
  margin-right: 1em;
}

.btnResume {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  border-radius: 5px;
  height: 50px;
  cursor: pointer;
  padding: 0px 30px;
  margin-top: 10px;
}

.btnResume:hover {
  background-color: var(--clr-primary);
  color: var(--clr-bg);
}

@media (max-width:1024px) {

  .app .home {
    align-items: flex-start;
    margin-top: 2em;
    justify-content: center;
    align-items: center;
  }

  .home> :nth-child(1) {
    font-size: 1.2rem;
  }

  .home> :nth-child(2) {
    font-size: 1rem;
  }

  .home> :nth-child(3) {
    font-size: 1.5rem;
  }

}


@media (max-width: 600px) {
  .mobileHead {
    align-content: center;
  }

  .app .home {
    align-items: flex-start;
    margin-top: 2em;
    justify-content: center;
    align-items: center;
  }

  .home> :nth-child(2) {
    font-size: 1rem;
  }

  .home> :nth-child(3) {
    font-size: 1.5rem;
  }


  .type {
    justify-content: center;
    margin: auto;
    width: 70%;

  }

}