.streak{
    margin:auto;
    width:80%;
    display:flex;
    align-items:center;
    justify-content:center;
    flex-direction:row;
    border-radius:10px 10px 10px 10px;
}

.streak>img{
width: 50%;
height: 300px;
}



@media (max-width: 600px) {

    .streak{
        flex-direction:column;
        width:80%;
        display:flex;
        align-items:center;
        justify-content:center;
    }
    
    .streak>img{
    width: 50%;
    height: 80px;
    }

}