.nav__list {
  margin-right: 1.5em;
  display: flex;
}

.nav__list-item {
  margin-left: 1.5em;
  color: var(--clr-primary);
  padding: 0 0 0.3em 0;
  position: relative;
}



.nav__list-item:hover{
  color: var(--clr-primary);
}


.nav__list-item::before {
  content: "";
  display: inline;
  width: 0%;
  height: 0.2em;
  position: absolute;
  bottom: 0;
  background-color: var(--clr-primary);
  transition: width 0.2s ease-in;
}


.nav__list-item:hover::before,
.nav__list-item:focus::before {
  width: 100%;
} 

.nav__list-item::after {
  transform: scaleX(0);
  height: 50px;
  width:100%;
  background-color: var(--clr-primary);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.nav__list-item:hover::after {
  color: var(--clr-primary);
  transform: scaleX(1);
  transform-origin: bottom left;
}

.app .nav__hamburger {
  display: none;
}


.home__name {
  color: var(--clr-primary);
}


.nav__theme {
  margin-top: 0.4em;
  color: var(--clr-primary);
}

@media (max-width: 600px) {
  .nav__list {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }

  .nav__list-item {
    margin: 0.5em 0;
  }

  .app .nav__hamburger {
    display: flex;
    z-index: 2;
    margin-left: 0.8em;
  }
}